import React, { useState } from "react";
import { useNavigate, useLocation } from "react-router-dom";
import Sidebar from "./Sidebar";
import "./ReportPage.css";

function ReportPage() {
    const [showPopup, setShowPopup] = useState(false);
    const [email, setEmail] = useState("");
    const [isSidebarOpen, setIsSidebarOpen] = useState(false);
    const navigate = useNavigate();
    const location = useLocation();

    // Get the report data and log details passed from Dashboard.js or DetailDisplay.js
    const { reportData, logDetails } = location.state || {};

    // Debugging: Log the reportData and logDetails to check if they're being received
    console.log("Report Data received in ReportPage:", reportData);
    console.log("Log Details received in ReportPage:", logDetails);

    const handleSendClick = () => {
        alert(`Report sent to ${email}`);
        setShowPopup(false);
        setEmail("");
    };

    const toggleSidebar = () => {
        setIsSidebarOpen(!isSidebarOpen);
    };

    const closeSidebar = () => {
        setIsSidebarOpen(false);
    };

    const handleBackClick = () => {
        if (location.state?.from === "detail-display") {
            navigate("/detail-display");
        } else {
            navigate("/dashboard", { state: { from: "report-page" } });
        }
    };

    // Parse the Gemini response and extract the report content
    const parseReportContent = (data) => {
        if (!data || !data.report_data) {
            console.error("Invalid report data:", data);
            return {
                executiveSummary: "No report data available.",
                keyDetails: "No key details available.",
                cveMapping: "No CVE mapping available.",
                actionableInsights: "No actionable insights available.",
            };
        }

        const reportText = data.report_data;

        // Extract sections using regex
        const executiveSummaryMatch = reportText.match(/### Executive Summary\n([\s\S]*?)\n###/);
        const keyDetailsMatch = reportText.match(/### Key Details\n([\s\S]*?)\n###/);
        const cveMappingMatch = reportText.match(/### CVE Mapping\n([\s\S]*?)\n###/);
        const actionableInsightsMatch = reportText.match(/### Actionable Insights\n([\s\S]*?)(\n###|$)/);

        return {
            executiveSummary: executiveSummaryMatch ? executiveSummaryMatch[1].trim() : "No executive summary available.",
            keyDetails: keyDetailsMatch ? keyDetailsMatch[1].trim() : "No key details available.",
            cveMapping: cveMappingMatch ? cveMappingMatch[1].trim() : "No CVE mapping available.",
            actionableInsights: actionableInsightsMatch ? actionableInsightsMatch[1].trim() : "No actionable insights available.",
        };
    };

    const { executiveSummary, keyDetails, cveMapping, actionableInsights } = parseReportContent(reportData);

    return (
        <div className="report-page">
            <Sidebar isOpen={isSidebarOpen} onClose={closeSidebar} />

            <button className="back-button" onClick={handleBackClick}>
                
            </button>

            <button className="hamburger-button" onClick={toggleSidebar}>
                &#9776;
            </button>

            <div className={`report-content ${isSidebarOpen ? "sidebar-open" : ""}`}>
                <h1 className="report-heading">Report</h1>

                <div className="report-details">
                    <h2>Date: {new Date().toLocaleDateString()}</h2>
                    <p>Prepared for: [Organization Name]</p>
                    <p>Prepared by: CCTI via Security Team</p>
                    <p>Log Details: {logDetails}</p> {/* Display the log details */}

                    <h3>1. Executive Summary</h3>
                    <p>{executiveSummary}</p>

                    <h3>2. Key Details</h3>
                    <p>{keyDetails}</p>

                    <h3>3. CVE Mapping</h3>
                    <p>{cveMapping}</p>

                    <h3>4. Actionable Insights</h3>
                    <p>{actionableInsights}</p>
                </div>

                <div className="button-group">
                    <button>Edit</button>
                    <button>Print</button>
                    <button>Save</button>
                    <button>Download</button>
                    <button onClick={() => setShowPopup(true)}>Share</button>
                    <button>Block IP</button>
                </div>
            </div>

            {/* Popup */}
            {showPopup && (
                <div className="popup">
                    <div className="popup-content">
                        <h2>Enter Email</h2>
                        <input
                            type="email"
                            value={email}
                            onChange={(e) => setEmail(e.target.value)}
                            placeholder="Enter email"
                        />
                        <button onClick={handleSendClick}>Send</button>
                    </div>
                </div>
            )}
        </div>
    );
}

export default ReportPage;